import Refresher from "./table/refresher";
import LayoutSwitcher from "./table/layout-switcher";
import { initAcServices } from "AC/helpers/admin-columns";
var AcServices = initAcServices();
AcServices.addListener("Table.Ready", function (e) {
    var table = e.table;
    table.Refresher = new Refresher(table);
    var switcher = document.querySelector('.layout-switcher');
    if (switcher) {
        var layout_switcher = new LayoutSwitcher(switcher);
        layout_switcher.place();
    }
    var form = table.getElement().closest('form');
    if (form) {
        form.insertAdjacentHTML('afterbegin', '<input type="hidden" name="ac-actions-form" value="1" />');
    }
});
